@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap");
.flag, .featured, .new {
  display: inline-block;
  text-transform: uppercase;
  font-size: 0.7rem;
  color: white;
  padding: 0.6em 0.6em;
  padding-bottom: 0.3em;
  margin-right: 0.5rem;
  border-radius: 15px;
}

.card, .cardFeatured {
  margin: 5vh auto;
  padding: 1.2rem;
  background-color: white;
  border-radius: 5px;
  box-shadow: 2px 8px 10px 1px rgba(0, 0, 0, 0.2);
}

.cardFeatured {
  border-left: 4px solid #5ba4a4;
}

.new {
  background-color: #5ba4a4;
}

.featured {
  background-color: #2c3a3a;
}

.jobDetail {
  text-align: left;
  border-bottom: #7b8e8e 1px solid;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}

.name {
  color: #5ba4a4;
  font-size: 0.9rem;
  font-weight: 700;
  margin-right: 0.8rem;
}

.jobTitle,
.jobInfo {
  color: #2c3a3a;
}

.jobTitle {
  font-weight: 700;
  margin: 0.75rem 0;
}
.jobTitle:hover {
  cursor: pointer;
  color: #5ba4a4;
}

.jobInfo {
  font-size: 0.8rem;
  color: #7b8e8e;
}
.jobInfo .info + .info {
  margin-left: 1rem;
}

.tablets {
  display: flex;
  flex-wrap: wrap;
  gap: 0.7rem;
}

@media screen and (min-width: 900px) {
  .card, .cardFeatured {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .jobDetail {
    border: none;
  }
}