@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap");
.tablet {
  width: max-content;
  font-size: 0.9375rem;
  font-weight: 700;
  padding: 0.5em 0.6em;
  color: #5ba4a4;
  background-color: #effafa;
  border-radius: 3px;
  display: flex;
  gap: 1rem;
}
.tablet:hover {
  background-color: #5ba4a4;
  color: white;
  cursor: pointer;
}